<template>
  <el-dialog
    class="popup-judgement"
    title="决裁申请"
    width="50%"
    :visible.sync="show"
    destroy-on-close
    :before-close="onCancel"
    @closed="onClose"
  >
    <div slot="title">
      决裁申请<span class="desc"><i>*</i>为必填项</span><appl-sts-icon :status="form.status" point />
    </div>

    <el-form
      ref="form"
      v-loading.lock="isLoading"
      :model="form"
      size="small"
      label-width="180px"
      :rules="rules"
      :hide-required-asterisk="false">
      <el-form-item label="申请时间：" style="margin-bottom: 8px;">{{ form.createTime | fmtCDate }}</el-form-item>
      <el-form-item label="积分账户名称：" style="margin-bottom: 8px;">{{ form.account }}</el-form-item>
      <el-form-item label="申请人：" style="margin-bottom: 8px;">{{ form.creater }}</el-form-item>
      <el-form-item label="选择申请种类：" prop="type">
        <el-select v-model="form.type">
          <el-option label="全部" :value="1"></el-option>
          <el-option label="预算额度" :value="2"></el-option>
          <el-option label="备用额度" :value="3"></el-option>
        </el-select>
      </el-form-item>
      <template v-if="form.type !== 3">
      <el-form-item label="新增预算（元）：" prop="budget">
        <el-input v-model="form.budget" placeholder="请输入新增预算（元）"></el-input>
      </el-form-item>
      <el-form-item label="新增预算额度：" prop="budgetAmount">
        <el-input v-model="form.budgetAmount" placeholder="请输入新增预算额度"></el-input>
      </el-form-item>
      </template>
      <template v-if="form.type !== 2">
      <el-form-item label="新增备用金（元）：" prop="imprest">
        <el-input v-model="form.imprest" placeholder="请输入新增备用金（元）"></el-input>
      </el-form-item>
      <el-form-item label="新增备用额度：" prop="imprestAmount">
        <el-input v-model="form.imprestAmount" placeholder="请输入新增备用额度"></el-input>
      </el-form-item>
      </template>
      <el-form-item label="申请理由：" prop="reason">
        <el-input
          v-model="form.reason"
          type="textarea"
          show-word-limit
          :maxlength="300"
          :rows="3"
          :autosize="{ minRows: 2, maxRows: 6 }"
          placeholder="请输入申请理由">
        </el-input>
      </el-form-item>
      <el-form-item label="附件上传：">
        <attachment :list="form.attachments" size="small"></attachment>
      </el-form-item>
      <el-form-item>
        <el-button plain @click="onCancel">取 消</el-button>
        <el-button type="primary" plain @click="onVerily">{{ isEditPage ? '保 存' : '提 交' }}</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
import Attachment from '@/components/Attachment'
import ApplStsIcon from '@/components/status/ApplStsIcon'
import { mapGetters } from 'vuex'

export default {
  name: 'popup-judgement',
  components: { Attachment, ApplStsIcon },
  model: {
    prop: 'visible',
    event: 'change'
  },
  props: {
    visible: Boolean,
    sid: [Number, String]
  },
  data () {
    return {
      isEditPage: false, // 是否编辑页
      show: false,
      isLoading: false,
      form: {}, // 表单数据
      rules: { // 表单验证规则
        type: [{ required: true, message: '请选择申请种类', trigger: 'change' }],
        budget: [{ required: true, pattern: /^\d{1,15}$/, message: '请填写整数数字，长度不能超出15个数字', trigger: 'blur' }],
        budgetAmount: [{ required: true, pattern: /^\d{1,15}$/, message: '请填写整数数字，长度不能超出15个数字', trigger: 'blur' }],
        imprest: [{ required: true, pattern: /^\d{1,15}$/, message: '请填写整数数字，长度不能超出15个数字', trigger: 'blur' }],
        imprestAmount: [{ required: true, pattern: /^\d{1,15}$/, message: '请填写整数数字，长度不能超出15个数字', trigger: 'blur' }],
        reason: [{ required: true, message: '请输入申请理由，长度不能超出300个汉字', trigger: 'blur' }]
      }
    }
  },
  computed: {
    ...mapGetters({
      accountInfo: 'getAccountInfo'
    })
  },
  watch: {
    visible (val) {
      this.show = val

      if (val) {
        // 根据是否有账户申请id，判断是创建还是编辑
        if (this.sid) {
          this.isEditPage = true
          this.getData(this.sid)
        } else {
          this.form = {
            createTime: new Date(),
            account: this.accountInfo.account,
            creater: this.accountInfo.name
          }
        }
      }
    }
  },
  methods: {
    getData (id) {
      this.isLoading = true
      this.$http.serviceGetApplEditDetail({ id })
        .then(res => {
          this.form = { ...res }
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    onClose () {
      this.$emit('update:visible', this.show)
    },

    // 提交前验证
    onVerily () {
      this.$refs.form.validate(valid => {
        if (valid && !this.overAmount) {
          this.$confirm(`是否${this.isEditPage ? '保存修改' : '确认提交'}？`)
            .then(() => {
              this.onSubmit()
            })
        } else {
          return false
        }
      })
    },

    // 提交申请
    onSubmit () {
      this.isLoading = true
      this.$http.serviceCreateAppl(this.form)
        .then(res => {
          this.$message.success('提交成功')
          this.show = false
        })
        .catch(error => {
          if (!error.code && error.name) Promise.reject(error)
          else this.$alert(error.message, '提交失败', { type: 'error' })
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    onCancel () {
      const _this = this
      this.$confirm(`是否取消${this.isEditPage ? '编辑' : '申请'}？`, '取消确认', { type: 'error' })
        .then(() => {
          _this.isEditPage = false
          _this.show = false
          _this.form = {}
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.popup-judgement {
  .desc {
    margin-left: 10px;
    font-size: 12px;
    font-weight: normal;
    color: #909399;
    i {
      color: #F56C6C;
    }
  }

  .el-select {
    width: 100%;
  }

  .el-button {
    min-width: 100px;
  }
}
</style>
