<template>
  <div class="amount-container">
    <div class="amount-inner">
      <!-- 直观图 -->
      <div class="amount-chart" :title="activitiesTips">
        <div class="amount-budget" :style="{ width: budgetWidth }">
          <div class="budget-line" :style="{ left: budgetLinePos }"></div>
          <div class="budget-used" :style="{ width: usedWidth }"></div>
          <div class="budget-issued" :style="{ width: issuedWidth }"></div>
        </div>
        <div class="amount-imprest" :style="{ width: imprestWidth }">
          <div class="imprest-line" :style="{ left: imprestLinePos }"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { fmtMoney, fmtFloat2Percent } from '@/filters'

export default {
  name: 'amount-bar',
  props: {
    data: {
      type: Object,
      required: true
    }
  },
  computed: {
    budgetWidth () {
      const w = fmtFloat2Percent(this.data.budgetAmount / (this.data.budgetAmount + this.data.imprestAmount))
      return w === '-' ? '50%' : w
    },
    imprestWidth () {
      return 100 - parseFloat(this.budgetWidth) + '%'
    },
    budgetLinePos () {
      return fmtFloat2Percent(this.data.budgetAmountLine)
    },
    imprestLinePos () {
      return fmtFloat2Percent(this.data.imprestAmountLine)
    },
    usedWidth () {
      return fmtFloat2Percent(this.data.usedAmount / (this.data.budgetAmount + this.data.imprestAmount))
    },
    issuedWidth () {
      return fmtFloat2Percent(this.data.issued / this.data.usedAmount)
    },
    activitiesTips () {
      return `预算额度：${fmtMoney(this.data.budgetAmount)}
预算费用（元）：${fmtMoney(this.data.budget)}
备用额度：${fmtMoney(this.data.imprestAmount)}
备用金（元）：${fmtMoney(this.data.imprest)}
已发行：${fmtMoney(this.data.issued)}
待发行：${fmtMoney(this.data.unIssued)}`
    }
  }
}
</script>

