            </template>

            <div class="activities-list">
              <el-carousel height="248px" :autoplay="false" arrow="never" indicator-position="none" ref="activities">
                <el-carousel-item v-for="(page, index) in activitiesList" :key="'page' + index">
                  <div class="activity-item" v-for="(item, iindex) in page" :key="'item' + iindex">
