                <el-popover content="统计本账户内所有活动的已冻结，已返还，已作废三种状态下，积分数量的分布情况">
                  <el-link :underline="false" icon="el-icon-question" slot="reference"></el-link>
                </el-popover>
              </span>
            </template>
            <chart-unissue-sts-overview :datas="issueStsOverview"></chart-unissue-sts-overview>
          </el-new-card>
        </el-col>
      </el-row>

      <!-- 统计筛选条件 -->
      <el-new-card>
        <el-form inline :model="searchForm" size="small" class="condition-bar">
          <el-form-item>
            <el-select v-model="searchForm.status" placeholder="活动状态">
              <el-option label="全部" value=""></el-option>
              <el-option label="已开始" :value="1"></el-option>
              <el-option label="已暂停" :value="2"></el-option>
              <el-option label="已重启" :value="3"></el-option>
              <el-option label="已结束" :value="4"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="searchForm.mode" placeholder="活动模式">
              <el-option label="全部" value=""></el-option>
              <el-option label="线上" :value="1"></el-option>
              <el-option label="线下" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="searchForm.bussiness" placeholder="业务归属">
              <el-option label="全部" value=""></el-option>
              <el-option label="客户业务" :value="1"></el-option>
              <el-option label="DLR业务" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="searchForm.type" placeholder="活动类型">
              <el-option label="全部" value=""></el-option>
              <el-option label="常规" :value="2"></el-option>
              <el-option label="节点" :value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="searchForm.name" placeholder="活动名称" multiple collapse-tags style="width: 200px" @change="searchNameChange">
              <el-option label="全部" value=""></el-option>
              <el-option :label="a.name" :value="a.id" v-for="a in actList" :key="'actList-' + a.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="searchForm.verifiSts" placeholder="认证状态">
              <el-option label="全部" value=""></el-option>
              <el-option label="已注册" :value="1"></el-option>
              <el-option label="未注册" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <date-range v-model="dateRange"></date-range>
          </el-form-item>
          <el-form-item>
            <el-button plain type="primary" :loading="isChartsLoading" @click="searchData" style="min-width: 75px;">查询</el-button>
          </el-form-item>
        </el-form>
      </el-new-card>

      <!-- 图表统计查询结果 -->
      <el-tabs type="border-card" v-if="pointsData.pointsIssued">
        <el-tab-pane label="积分发行/使用情况统计" lazy>
          <div class="charts-header">{{ fmtCDateRange }}<el-link :underline="false" icon="el-icon-download"></el-link></div>
          <div class="charts-box">
            <chart-points-issued :datas="pointsData.pointsIssued"></chart-points-issued>
            <el-divider></el-divider>
            <chart-points-usage :datas="pointsData.pointsUsage"></chart-points-usage>
            <el-divider></el-divider>
            <chart-points-unused :datas="pointsData.pointsUnused"></chart-points-unused>
            <el-divider></el-divider>
            <chart-points-verified :datas="pointsData.pointsVerified"></chart-points-verified>
            <el-divider></el-divider>
            <chart-points-recyled :datas="pointsData.pointsRecyled"></chart-points-recyled>
            <el-divider></el-divider>
            <chart-points-chain :datas="pointsData.pointsChain"></chart-points-chain>
            <el-divider></el-divider>
            <chart-points-growth-rate :datas="pointsData.pointsGrowthRate"></chart-points-growth-rate>
            <el-divider></el-divider>
            <chart-points-verified-src :datas="pointsData.pointsVerifiedSrc"></chart-points-verified-src>
            <el-divider></el-divider>
            <chart-points-recyled-src :datas="pointsData.pointsRecyledSrc"></chart-points-recyled-src>
          </div>
        </el-tab-pane>
        <el-tab-pane label="业务归属统计" lazy>
          <div class="charts-header">{{ fmtCDateRange }}<el-link :underline="false" icon="el-icon-download"></el-link></div>
          <div class="charts-box">
            <chart-bus-chain :datas="bussinessData.busChain"></chart-bus-chain>
            <el-divider></el-divider>
            <chart-bus-growth-rate :datas="bussinessData.busGrowthRate"></chart-bus-growth-rate>
          </div>
        </el-tab-pane>
        <el-tab-pane label="活动效果统计" lazy>
          <div class="charts-header">{{ fmtCDateRange }}<el-link :underline="false" icon="el-icon-download"></el-link></div>
          <div class="charts-box">
            <chart-part-user :datas="activityEffectData.partUser"></chart-part-user>
            <el-divider></el-divider>
            <chart-part-user-chain :datas="activityEffectData.partUserChain"></chart-part-user-chain>
            <el-divider></el-divider>
            <chart-part-times :datas="activityEffectData.partTimes"></chart-part-times>
            <el-divider></el-divider>
            <chart-part-times-chain :datas="activityEffectData.partTimesChain"></chart-part-times-chain>
          </div>
        </el-tab-pane>
        <el-tab-pane label="活动类型/状态统计" lazy>
          <div class="charts-header">{{ fmtCDateRange }}<el-link :underline="false" icon="el-icon-download"></el-link></div>
          <div class="charts-box">
            <chart-act-mode :datas="activityTypeData.actMode"></chart-act-mode>
            <el-divider></el-divider>
            <chart-act-bus :datas="activityTypeData.actBus"></chart-act-bus>
            <el-divider></el-divider>
            <chart-act-type :datas="activityTypeData.actType"></chart-act-type>
            <el-divider></el-divider>
            <chart-act-status :datas="activityTypeData.actStatus"></chart-act-status>
          </div>
        </el-tab-pane>
        <el-tab-pane label="结算统计" lazy>
          <div class="charts-header">{{ fmtCDateRange }}<el-link :underline="false" icon="el-icon-download"></el-link></div>
          <div class="charts-box">
            <chart-stmt-overview :datas="settlementData.overview"></chart-stmt-overview>
            <el-divider></el-divider>
            <chart-stmt-sts-rec :datas="settlementData.stsRec"></chart-stmt-sts-rec>
            <el-divider></el-divider>
            <chart-stmt-sts-pay :datas="settlementData.stsPay"></chart-stmt-sts-pay>
            <el-divider></el-divider>
            <chart-stmt-dept-rec :datas="settlementData.deptRec"></chart-stmt-dept-rec>
            <el-divider></el-divider>
            <chart-stmt-dept-pay :datas="settlementData.deptPay"></chart-stmt-dept-pay>
            <el-divider></el-divider>
            <chart-stmt-records :search="searchForm"></chart-stmt-records>
          </div>
        </el-tab-pane>
        <el-tab-pane label="商品统计" lazy>
          <div class="charts-header">{{ fmtCDateRange }}<el-link :underline="false" icon="el-icon-download"></el-link></div>
          <div class="charts-box">
            <chart-goods :datas="goodsData.goods"></chart-goods>
            <el-divider></el-divider>
            <chart-goods-chain :datas="goodsData.goodsChain"></chart-goods-chain>
            <el-divider></el-divider>
            <chart-goods-verified-source :datas="goodsData.verifiedSource"></chart-goods-verified-source>
          </div>
        </el-tab-pane>
      </el-tabs>

      <popup-lmt-chg-rec :visible.sync="chgRecVisible" :id="id" @popupAct="popupActIntro" @popupDtl="popupJudgementDetail"></popup-lmt-chg-rec>
      <popup-use-imp-rec :visible.sync="useRecVisible" :id="id" @popupDtl="popupUseImpDetail"></popup-use-imp-rec>
      <popup-act-intro :visible.sync="actIntroVisible" :aid="selectedId"></popup-act-intro>
      <popup-add-amt-detail :visible.sync="addAmtDtlVisible" :sid="selectedId" @popupEdit="popupAddAmt"></popup-add-amt-detail>
      <popup-use-imp-detail :visible.sync="useImpDtlVisible" :sid="selectedId" @popupEdit="popupUseImp"></popup-use-imp-detail>
      <popup-judgement-detail :visible.sync="judgementDtlVisible" :sid="selectedId" @popupEdit="popupJudgement"></popup-judgement-detail>
      <popup-add-amt :visible.sync="addAmtVisible" :sid="selectedId"></popup-add-amt>
      <popup-use-imp :visible.sync="useImpVisible" :sid="selectedId"></popup-use-imp>
      <popup-judgement :visible.sync="judgementVisible" :sid="selectedId"></popup-judgement>
      <popup-atta :visible.sync="attaVisible" :fileList="info.attachments"></popup-atta>
    </template>

    <router-view v-else />
  </div>
</template>

<script>
import ElNewCard from '@/components/ElNewCard'
import TodayData from '@/components/TodayData'
import InfoBlock from '@/components/InfoBlock'
import InfoItem from '@/components/InfoItem'
import AmountAcct from '@/components/amounts/AmountAcct'
import AmountBar from '@/components/amounts/AmountBar'
import AcctStsIcon from '@/components/status/AcctStsIcon'
import ToDoList from '@/components/ToDoList'
import DateRange from '@/components/DateRange'
import chartMixins from '@/components/charts/mixins'
import moment from 'moment'
import PopupLmtChgRec from '@/views/popups/PopupLmtChgRec'
import PopupUseImpRec from '@/views/popups/PopupUseImpRec'
import PopupActIntro from '@/views/popups/PopupActIntro'
import PopupAddAmtDetail from '@/views/popups/PopupAddAmtDetail'
import PopupUseImpDetail from '@/views/popups/PopupUseImpDetail'
import PopupJudgementDetail from '@/views/popups/PopupJudgementDetail'
import PopupAddAmt from '@/views/popups/PopupAddAmt'
import PopupUseImp from '@/views/popups/PopupUseImp'
import PopupJudgement from '@/views/popups/PopupJudgement'
import PopupAtta from '@/views/popups/PopupAtta'
import { mapGetters } from 'vuex'

export default {
  name: 'account-detail',
  mixins: [chartMixins],
  components: {
    ElNewCard,
    TodayData,
    InfoBlock,
    InfoItem,
    AmountAcct,
    AmountBar,
    AcctStsIcon,
    ToDoList,
    DateRange,
    PopupLmtChgRec,
    PopupUseImpRec,
    PopupActIntro,
    PopupAddAmtDetail,
    PopupUseImpDetail,
    PopupJudgementDetail,
    PopupAddAmt,
    PopupUseImp,
    PopupJudgement,
    PopupAtta
  },
  data () {
    return {
      id: 0, // 当前账户id
      gutter: 40, // 面板之间的间距
      amountData: {}, // 额度概览
      toDo: [], // 待办事项
      todayData: [], // 本日数据
      actList: [], // 活动名称列表
      searchForm: { // 搜索条件
        status: null,
        mode: null,
        bussiness: null,
        type: null,
        name: [],
        verifiSts: null,
        startTime: null,
        endTime: null
      },
      dateRange: [], // 搜索日期范围
      isLoading: false,
      isChartsLoading: false,
      fmtCDateRange: '', // 统计图表统计时间格式化展示
      info: {}, // 账户信息
      activitiesList: [], // 进行中的活动
      curStsOverview: [], // 积分状态概览
      issueStsOverview: [], // 未发行积分状态概览
      selectedId: 0,
      chgRecVisible: false,
      useRecVisible: false,
      actIntroVisible: false,
      addAmtDtlVisible: false,
      useImpDtlVisible: false,
      judgementDtlVisible: false,
      addAmtVisible: false,
      useImpVisible: false,
      judgementVisible: false,
      attaVisible: false
    }
  },
  computed: {
    ...mapGetters({
      accountInfo: 'getAccountInfo'
    }),

    isEditor () {
      return this.info.deptId === this.accountInfo.orgId
    },

    isGTMC () {
      return this.accountInfo.orgId === 1
    }
  },
  watch: {
    dateRange: function (val) {
      this.searchForm.startTime = val[0]
      this.searchForm.endTime = val[1]
    },

    isChartsLoading: function (val) {
      if (!val) {
        const startTime = this.dateRange[0] ? moment(this.dateRange[0]).format('YYYY年MM月DD日 HH时mm分') : ''
        const endTime = moment(this.dateRange[1] ? this.dateRange[1] : moment()).format('YYYY年MM月DD日 HH时mm分')
        this.fmtCDateRange = startTime + ' 至 ' + endTime
      }
    }
  },
  created () {
    this.id = this.$route.query.id
    if (this.id) {
      this.getData(this.id)
    } else {
      this.$router.replace('/404')
    }
  },
  methods: {
    // 获取详情信息
    getData (id) {
      this.isLoading = true
      Promise.all([
        this.$http.serviceGetAcctDetail({ id }),
        this.$http.serviceGetAcctActList({ id })
      ])
        .then(res => {
          // 积分信息
          this.info = res[0].info
          this.amountData = res[0].amountData
          this.todayData = res[0].todayData
          this.toDo = res[0].toDoList
          this.curStsOverview = res[0].pointsOverview
          this.issueStsOverview = res[0].unissuedOverview
          this.activitiesList = res[0].underWayAct
          // 活动名称列表
          this.actList = res[1]
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    // 查看进行中活动的上翻页
    onPrevActPage () {
      this.$refs.activities.prev()
    },

    // 查看进行中活动的下翻页
    onNextActPage () {
      this.$refs.activities.next()
    },

    // 跳转到查看报表
    toReport () {
      this.$router.push({
        path: '/crdtAcctMgmt/acctDetail/acctReport',
        query: {
          id: this.$route.query.id
        }
      })
    },

    // 跳转到查看明细页面
    toCrtdDetail () {
      this.$router.push({
        path: '/crdtAcctMgmt/acctDetail/crdtDetail',
        query: {
          id: this.$route.query.id
        }
      })
    },

    // 弹出额度变更记录弹窗
    popupChgRec () {
      this.chgRecVisible = true
    },

    // 弹出备用额度使用申请记录弹窗
    popupUseRec () {
      this.useRecVisible = true
    },

    // 弹出活动简介弹窗
    popupActIntro (aid) {
      this.selectedId = aid
      this.actIntroVisible = true
    },

    // 弹出提额申请详情
    popupAddAmtDetail (aid) {
      this.selectedId = aid
      this.addAmtDtlVisible = true
    },

    // 弹出决裁申请详情
    popupJudgementDetail (aid) {
      this.selectedId = aid
      this.judgementDtlVisible = true
    },

    // 弹出备用使用申请详情
    popupUseImpDetail (aid) {
      this.selectedId = aid
      this.useImpDtlVisible = true
    },

    // 弹出申请额度弹窗
    popupAddAmt (applId) {
      this.selectedId = typeof applId !== 'object' ? applId : null
      this.addAmtVisible = true
    },

    // 弹出备用额度使用申请弹窗
    popupUseImp (applId) {
      this.selectedId = typeof applId !== 'object' ? applId : null
      this.useImpVisible = true
    },

    // 弹出决裁申请弹窗
    popupJudgement (applId) {
      this.selectedId = typeof applId !== 'object' ? applId : null
      this.judgementVisible = true
    },

    // 查看附件
    popAttachments () {
      this.attaVisible = true
    },

    // 查询统计数据
    searchData () {
      this.isChartsLoading = true
      this.$http.serviceChartsData(this.searchForm)
        .then(res => {
          this.pointsData = res.pointsData
          this.bussinessData = res.bussinessData
          this.activityEffectData = res.activityEffectData
          this.activityTypeData = res.activityTypeData
          this.settlementData = res.settlementData
          this.goodsData = res.goodsData
        })
        .finally(() => {
          this.isChartsLoading = false
        })
    },

    // 处理活动名下拉复选的全选问题
    searchNameChange (val) {
      if (val.some(v => v === '')) {
        this.searchForm.name = ['']
      }
    }
  }
}
</script>

