    display: flex;
    justify-content: space-between;
    padding: 10px 0;

    .el-button { min-width: 120px; }
  }

  .comparative-data {
    margin: -10px 0;
    strong {
      font-size: 18px;
      font-weight: normal;
    }
    p {
      display: flex;
      justify-content: space-between;
      margin-top: 10px;
      margin-bottom: 0;
      font-size: 12px;

      span.increase { color: #19be6b; }
      span.reduce { color: #ed4014; }
    }
  }

  .activities-nums {
    font-size: 12px;
    font-weight: normal;
    color: #606266;
  }
  .activity-item {
    margin-bottom: 4px;
    font-size: 12px;
    i {
      font-style: normal;
      vertical-align: middle;
    }
    .el-link {
      font-size: 12px;
    }
  }
